  margin-right: 14px;
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 21px 0 32px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

@media (max-width: 991px) {
  .footer__bottom {
    font-size: 14px;
  }
}

@media (max-width: 767px) {
  .footer__bottom {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px 0px 25px;
  }
}

@media (max-width: 567px) {
  .footer__bottom {
    font-size: 11px;
  }
}

.footer__bottom a {
  border-bottom: 1px solid #fff;
  transition-duration: 0.3s;
  transition-property: color, border-color;
}

.footer__bottom a:hover {
  color: #f8b750;
  border-color: #f8b750;
}

.footer__bottom p {
  margin: 0;
}

.footer__bottom > a,
.footer__bottom > p {
  max-width: 30%;
  text-align: center;
}

@media (max-width: 767px) {
  .footer__bottom > a,
  .footer__bottom > p {
    max-width: 100%;
    margin-bottom: 15px;
  }
}
